<template>
    <div class="back-cont" >
        <img class="top-bg-cont" src="../assets/back_top.svg" />
    <b-container fluid class="cont">
            
        <div class="login-form" style="background-color: turqoise;">
            <div class="heading"><h2 class="login-text">Login</h2> <span class = "desc">(Internship Account)</span> </div>
            
            <div ref="warning" class="form-login-warnings" v-if="errors!=''">{{errors}}</div>
            <b-form @submit="onSubmit">
                <b-form-group
                    id="form-login-email"
                    class="text-input"
                    label="Email"
                    label-for="login-email"
                >
                    <b-form-input
                        id="login-email"
                        v-model="login.email"
                        placeholder="Enter your email"
                        required
                    >
                    </b-form-input>
                </b-form-group>
                <b-form-group
                    id="form-login-password"
                    class="text-input"
                    label="Password"
                    label-for="login-pass"
                >
                    <b-form-input
                        class="mb-4"
                        id="login-pass"
                        v-model="login.pass"
                        type="password"
                        placeholder="Enter your password"
                        required
                    >
                    </b-form-input>
                </b-form-group>
                <b-form-checkbox
                        class="mb-4 input-check"
                        id="login-remember"
                        v-model="login.remember"
                    >Remember my account</b-form-checkbox>
                <b-button
                    type="submit"
                    variant="primary"
                    ref="submitbutton"
                    class="sirkel-blue"
                    id="login-button"
                >
                    Login
                </b-button>
            </b-form>
        </div>
    
    </b-container>
    &nbsp;
    <img
      style="width: 100%; margin-top: 87px; margin-bottom: -1px"
      src="../assets/back_img-1.svg"
    />
    </div>
    
    
</template>

<script>
import axios from 'axios';
export default {
    
    data() {
        return {
            login: {
                name: '',
                pass: '',
                remember: false,
            },
            errors: ''
        }
    },
    //oncreated check cookies ke api, kalau valid redirect ke home
    beforeCreate() {
        //use vuex later
        if ((window.$cookies.isKey('token')|this.$session.has('token'))){
            window.location.href = "/dashboard";
        }
    },
    methods: {
        onSubmit(e) {
            e.preventDefault();
            
            this.$refs.submitbutton.disabled = true;
            //send email and password
            this.errors = '';
            axios.post(process.env.VUE_APP_API_URL + '/api/login-freelancer', {email: this.login.email, password:this.login.pass})
            .then(res => {
                // this.afterSubmit(res.data);
                this.$refs.submitbutton.disabled = false;
                if (this.login.remember) {
                    window.$cookies.set("token", res.data.token, "1d");
                }else{
                    this.$session.set("token", res.data.token);
                }
                window.location.href = "/dashboard";
            })
            .catch(err => {
                this.$refs.submitbutton.disabled = false;
                console.log(err);
                this.errors = "The name and password you entered did not match our records. Please check and try again...";
            });
            
        },
    }

}
</script>


